}

.footer-form__inputs {
  margin-bottom: 25px;
}
.footer-form__inputs input {
  width: 100%;
  min-height: 56px;
  margin-bottom: 6px;
  padding: 15px 21px;
  font-size: 18px;
  line-height: 120%;
  background-color: #FFFFFF;
  border: none;
  border-radius: 50px;
  transition: 0.3s ease-in-out color;
}
.footer-form__inputs input:hover {
  text-decoration: underline;
}
.footer-form__inputs input:focus {
  text-decoration: none;
  outline: solid 2px #FF9200;
}
.footer-form__inputs input:last-child {
  margin-bottom: 0;
}
.footer-form__inputs input::placeholder {
  color: #ABB7C1;
  transition: 0.3s ease-in-out color;
}
.footer-form__inputs input:hover::placeholder {
  color: #333333;
}

.footer-form__btn {
  margin-bottom: 26px;
}

.footer-form__checkbox {
  position: relative;
  text-align: left;
  padding-left: 40px;
}
.footer-form__checkbox label {
  font-size: 12px;
  line-height: 14px;
  cursor: pointer;
}
.footer-form__checkbox input[type=checkbox] {
  left: 10px;
  top: 20px;
}
.footer-form__checkbox input + label::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  border: 2px solid #54d3fc;
  border-radius: 2px;
  left: 0px;
  top: 8px;
  cursor: pointer;
}
.footer-form__checkbox input:checked + label::after {
  content: "";
  position: absolute;
  width: 9px;
  height: 6px;
  border: 2px solid #54D3FC;
  border-top: none;
  border-right: none;
  transform: rotate(-45deg);
  left: 6px;
  top: 13px;
  cursor: pointer;
}
.footer-form__checkbox input:hover + label::before,
.footer-form__checkbox input:hover + label::after,
.footer-form__checkbox input:focus + label::before,
.footer-form__checkbox input:focus + label::after {
  border-color: rgba(84, 211, 252, 0.3);
}
.footer-form__checkbox input:active + label::before,
.footer-form__checkbox input:active + label::after {
  opacity: 0.6;
}

