.page__header {
  position: relative;
}

.hero-section::before {
  content: "";
  width: 100%;
  min-height: 592px;
  position: absolute;
  top: 0;
  mask-image: url("../../img/hero-section-img/header-bg-mobile.svg");
  mask-repeat: no-repeat;
  mask-size: 100% 100%;
  mask-position: center;
  z-index: -1;
  background: linear-gradient(180deg, #00549A 0%, #20A0C9 23.91%);
}
@media (min-width: 371px) {
  .hero-section::before {
    mask-image: url("../../img/hero-section-img/header-bg-desktop.svg");
  }
}
@media (min-width: 1024px) {
  .hero-section::before {
    min-height: 650px;
  }
}
@media (min-width: 1440px) {
  .hero-section::before {
    min-height: 700px;
  }
}
@media (min-width: 1920px) {
