.btn {
  width: 100%;
  min-height: 56px;
  padding: 5px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 500;
  font-size: 20px;
  line-height: 23px;
  color: #FFFFFF;
  background: linear-gradient(99.01deg, #FF9200 4.22%, #FFCC00 108.35%);
  box-shadow: 0px 4px 5px #FF9200, 0px 10px 15px #1678AC;
  border: none;
  border-radius: 50px;
  cursor: pointer;
}
.btn:hover, .btn:focus {
  background: #FFCC00;
  border: 1px solid rgba(0, 0, 0, 0.2);
}
.btn:active {
  bottom: -2px;
  box-shadow: 0px 2px 5px #FF9200, 0px 8px 15px #1678AC;
}
