.hero-section {

  &::before {
    content: "";
    width: 100%;
    min-height: 592px;
    position: absolute;
    top: 0;
    mask-image: url("../../img/hero-section-img/header-bg-mobile.svg");
    mask-repeat: no-repeat;
    mask-size: 100% 100%;
    mask-position: center;
    z-index: -1;
    background: $hero_gradient;

    @media (min-width: 371px) {
        mask-image: url("../../img/hero-section-img/header-bg-desktop.svg");
    }

    @include vp--1024 {
      min-height: 650px;
    }

    @include vp--1440 {
      min-height: 700px;
    }

    @include vp--1920 {
      min-height: 916px;
    }
  }
}

.hero-section__container {
  overflow: hidden;

  @include vp--1024 {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: flex-start;
    align-content: flex-start;
    justify-content: space-between;
  }
}

.hero-section__inner {
  @include vp--1024 {
    margin-bottom: 56px;
    width: 60%;
    padding-right: 20px;
  }
}

.hero-section__title,
.hero-section__subtitle {
  @include reset-text;

  color: $white;
  padding-bottom: 27px;
}

.hero-section__title {
  @include font(30px, 37px, 500, normal);

  @include vp--1024 {
    @include font(60px, 70px, 500, normal);
    padding-bottom: 47px;
  }
}

.hero-section__subtitle {
  @include font(16px, 21px, 400, normal);

  & br {
    display: none;
  }

  @include vp--1024 {
    @include font(20px, 26px, 400, normal);
    padding-bottom: 52px;

    & br {
      display: block;
    }
  }
}

.hero-section__description {
  @include reset-text;
  @include font(16px, 26px, 400, normal);
  color: $text_main;
  letter-spacing: -0.1px;
  padding-bottom: 23px;

  @include vp--1024 {
    @include font(18px, 32px, 400, normal);
    text-align: center;
    padding: 50px 20px;

    & br {
      display: none;
    }
  }
}

.hero-section__img {
  margin-top: 45px;
  margin-bottom: 18px;
  margin-left: 22px;

  @include vp--1024 {
    width: 354px;
    height: 284px;
    margin: 90px 0 0;
  }

  @media (min-width: 1300px) {
    width: 468px;
    height: 409px;
    margin: 45px 0 0;
  }

  @include vp--1440 {
    width: 929px;
    height: 753px;
  }
}
